<template>
    <div>
        <div class="row" v-if="this.$store.getters.loadData.Status === 200">
            <div class="col-12 col-lg-6 col-xl-4">
                <button class="w-100 btn btn-secondary back-button mb-5" @click="backToMain">Back</button>
            </div>
            <div class="col-12">
                <div class="d-block d-lg-flex justify-content-center justify-content-lg-start align-items-center mb-4">
                    <div class="img-wrapper mb-3 mb-lg-0 mx-lg-0 me-lg-3">
                        <img class="logo" :src="logo" onerror="javascript:this.src='https://www.freeiconspng.com/uploads/no-image-icon-4.png'"/>
                    </div>
                    <h2 class="text-center text-lg-start">
                        <span class="title"><strong>{{ this.data["Counter"].replaceAll(',','').replaceAll('.','') }}</strong></span>
                    </h2>
                </div>
            </div>
            <div class="col-12 col-lg-6 col-xxl-4" v-if="this.dividends">
                <h3 class="text-center text-lg-start">Dividend Analysis</h3>
                <table class="table table-hover">
                    <tr>
                        <th>All-Time Average Yield Based on Current price</th>
                        <td>{{this.data["Dividend Analysis"]["All-Time Average Yield Based on Current price"]}}</td>
                    </tr>
                    <tr>
                        <th>All-Time Average Dividend per Payout</th>
                        <td>{{this.data["Dividend Analysis"]["All-Time Average Dividend per Payout"]}}</td>
                    </tr>
                    <tr>
                        <th>Total Dividend Payment Count</th>
                        <td>{{this.data["Dividend Analysis"]["Total Dividend Payment Count"]}}</td>
                    </tr>
                    <tr>
                        <th>Current Dividend Paying Status</th>
                        <td :class="{'red': !divPayingStatus, 'green': divPayingStatus}">
                            <div class="d-flex">
                                <img class="star" :src="divPayingStatusPic"/>
                                {{ divPayingStatus ? 'ACTIVE' : 'INACTIVE' }}
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <th>Dividend Suspension Strike</th>
                        <td :class="{'green': mode === 0, 'orange': mode >= 1 && mode < 3, 'red': mode >= 3}">
                            <div class="d-flex">
                                <img class="star" :src="star" />
                                {{ mode === 0 ? 'CLEAN' : ''}}
                                {{ mode &gt;= 1 && mode &lt; 3 ? mode + ' hit': '' }}
                                {{ mode >= 3 ? mode + ' hit': '' }}
                            </div>
                        </td>
                    </tr>
                    <tr v-if="isYearsMissing">
                        <th>Dividend Paying Frequency</th>
                        <td>{{ frequency }}</td>
                    </tr>
                    <tr v-else>
                        <th>Last Dividend Paid</th>
                        <td>{{this.data["Dividend Analysis"]["Last Dividend Paid since(years)"].toFixed(1)}} years ago</td>
                    </tr>
                </table>
            </div>
            <hr v-if="this.dividends"/>
            <div class="col-12 col-lg-6 col-xxl-4">
                <h3 class="text-center text-lg-start">Fundamentals</h3>
                <table class="table table-hover">
                    <tr>
                        <th>Sector</th>
                        <td>{{this.data.Fundamentals.Sector}}</td>
                    </tr>
                    <tr>
                        <th>Current Price</th>
                        <td>{{this.data["Last Done Price"]}}</td>
                    </tr>
                    <tr>
                        <th>Trailing PE</th>
                        <td>{{this.data.Fundamentals["Trailing PE"]}}</td>
                    </tr>
                    <tr>
                        <th>Total Cash Balance (mrq)</th>
                        <td>{{this.data.Fundamentals["Total Cash Balance (mrq)"]}}</td>
                    </tr>
                    <tr>
                        <th>Book Value</th>
                        <td>{{this.data.Fundamentals["Book Value"]}}</td>
                    </tr>
                    <tr>
                        <th>Net Income (ttm)</th>
                        <td>{{this.data.Fundamentals["Net Income (ttm)"]}}</td>
                    </tr>
                    <tr>
                        <th>Total Debt (mrq)</th>
                        <td>{{this.data.Fundamentals["Total Debt (mrq)"]}}</td>
                    </tr>
                    <tr v-if="isYearsMissing">
                        <th>Forward Dividend Yield</th>
                        <td>{{this.divYield}}</td>
                    </tr>
                </table>
            </div>
            <div class="col-12" v-if="!this.dividends">
                <h4 class="remark text-center text-lg-start">{{this.data["Counter"].replaceAll(',','').replaceAll('.','')}} doesn't pays dividend.</h4>
            </div>
            <hr v-if="this.dividends">
            <div class="col-12 col-lg-6 col-xxl-4" v-if="this.dividends">
                <h3 class="text-center text-lg-start">Dividend History</h3>
                <table class="table table-hover table-vertical">
                    <thead>
                        <tr>
                            <th>Date Announcement</th>
                            <th>Dividend Amount</th>
                        </tr>
                    </thead>
                    <tbody v-for="dividend in this.dividendHistory" :key="dividend.id" :id="dividend.id">
                        <tr>
                            <td>{{dividend["Annoucement Date"]}}</td>
                            <td>{{dividend["Dividend Amount"]}}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <div class="row" v-else>
            <div class="col-12 col-lg-6 col-xl-4">
                <button class="w-100 btn btn-secondary back-button mb-5" @click="backToMain">Back</button>
            </div>
            <div class="col-12">
                <invalid-stocks :data="this.data.counter" :error="this.error"></invalid-stocks>
            </div>
        </div>
    </div>
</template>
<script>
import InvalidStocks from './InvalidStocks.vue';
export default {
    props:['error'],
    components: {
        InvalidStocks,
    },
    data(){
        return {
            data: [],
            logo: '',
            dividends: false,
            isYearsMissing: false,
            dividendHistory: [],
            status: '',
            divYield: '',
            totalDiv: '',
            star: '',
            mode: '',
            divPayingStatus: false,
            divPayingStatusPic: '',
            frequency:'',
            serverResponse: null,
        };
    },
    created(){
        this.getData();
        this.setDivYield();
        this.isError();
    },
    updated(){
        this.isError();
    },
    computed: {
        checkIsError(){
            return this.$store.getters.isError.error;
        },
    },
    methods: {
        isError(){
            this.serverResponse = this.$store.getters.isError;
        },
        getData(){
            this.dividends = this.data["Stock Type"] === 'Dividend';
            this.data = this.$store.getters.loadData;
            if(this.data.Status === 200 && this.data["Stock Type"] === 'Dividend'){
                this.logo = this.$store.getters.loadData["Company Logo"];
                this.dividends = this.data["Stock Type"] === 'Dividend';
                this.dividendHistory = this.data["Dividend History"];
                this.mode = +this.data["Dividend Suspension Count"];
                this.divPayingStatus = this.data["Dividend Analysis"]["Actively Paying"];
                this.frequency = this.data["Dividend Frequency"].toLowerCase().trim();
                if(this.dividends){
                    this.isYearsMissing = this.$store.getters.loadData["Dividend Analysis"]["Actively Paying"];
                }
                if(this.mode === 0){
                    this.star = 'https://img.icons8.com/flat-round/128/star--v1.png';
                } else if(this.mode >= 1 && this.mode < 3){
                    this.star = 'https://img.icons8.com/fluency/96/box-important.png';
                } else {
                    this.star = 'https://img.icons8.com/external-others-iconmarket/128/external-danger-traffic-signs-others-iconmarket-2.png';
                }

                if(this.divPayingStatus){
                    return this.divPayingStatusPic = 'https://img.icons8.com/flat-round/128/star--v1.png';
                } else {
                    return this.divPayingStatusPic = 'https://img.icons8.com/external-others-iconmarket/128/external-danger-traffic-signs-others-iconmarket-2.png';
                }
            }

            if(this.data.Status === 200 && this.data["Stock Type"] === 'Non-Dividend'){
                this.logo = this.$store.getters.loadData["Company Logo"];
                this.data = this.$store.getters.loadData;
            } 
            
            if(this.data.Status === 404){
                this.data = this.$store.getters.loadData;
                this.status = this.data.status;
            }  
        },
        setDivYield(){
            if(this.$store.getters.loadData["Dividend Frequency"] === 'Monthly'){
                this.totalDiv = parseFloat(this.$store.getters.loadData["Dividend History"][0]["Dividend Amount"]) * 12;
            }
            if(this.$store.getters.loadData["Dividend Frequency"] === 'Quarterly'){
                this.totalDiv = parseFloat(this.$store.getters.loadData["Dividend History"][0]["Dividend Amount"]) * 4;
            }
            if(this.$store.getters.loadData["Dividend Frequency"] === 'Bi-Annually'){
                this.totalDiv = parseFloat(this.$store.getters.loadData["Dividend History"][0]["Dividend Amount"]) * 2;
            }
            if(this.$store.getters.loadData["Dividend Frequency"] === 'Annually'){
                this.totalDiv = parseFloat(this.$store.getters.loadData["Dividend History"][0]["Dividend Amount"]) * 1;
            }
            
            return this.divYield = ((this.totalDiv/this.$store.getters.loadData["Last Done Price"])*100).toFixed(2) + '%';
        },
        backToMain(){
            this.data = [];
            this.$store.commit('loadData', []);
            if(this.error){
                this.$store.dispatch('isError', {
                    error: false,
                    status: null,
                });
                return this.$store.dispatch('dataLoaded', false);
            }
            return this.$store.dispatch('dataLoaded', false);
        }
    },
}
</script>
<style scoped>
span.title {
    color: #49c1ff;
}

img.star {
    width: 35px;
    margin-right: 5px;
}

div.d-flex {
    justify-content: center;
    align-items: center;
}

.green{
    color: rgba(0, 128, 0, 0.737);
    transition: all 0.1s ease-in;
}

.red {
    color: #fd1100;
    transition: all 0.1s ease-in;
}

.orange {
    color: rgba(221, 109, 5, 0.93);
    transition: all 0.1s ease-in;
}

.red:hover, .green:hover, .orange:hover {
    transform: scale(1.15);
}

.remark {
    color:#f5a5a5;
}

table {
    color: #333;
    background: #ccc;
    border-radius: 12px;    
}

table.table-vertical, table td{
    text-align: center;
    font-weight: bold;
}

.table>:not(:last-child)>:last-child>* {
    border: 1px solid transparent;
}

.table>:not(caption)>*>* {
    box-shadow: unset;
    padding: 2px 5px;
}

th,td {
    padding: 2px 5px;
    border: 1px solid transparent;
}

th {
    background-color: #88b0e6 !important;
    color: #333;
}

tr:first-child th, .table-vertical th:first-child {
    border-radius: 12px 0px 0px 0px;
}

tr:last-child th, .table-vertical td:first-child {
    border-radius: 0px 0px 0px 12px;
}

.table-vertical th:last-child {
    border-radius: 0px 12px 0px 0px;
}
 
hr:not([size]) {
    height: 5px;
    color: #ccc;
    margin: 30px auto;
}
.img-wrapper {
    background: #fff;
    width: fit-content;
    border-radius: 50%;
    padding: 20px;
    margin: 0 auto;
}
img.logo {
    width: 80px;
    height: 80px;
}

/* Light Mode */
.light span.title {
    color: #003479;
}

.light .remark {
    color:#940f0f;
}

.light th {
    background-color: #003479 !important;
    color: #ccc;
}

.light hr:not([size]) {
    color: #333;
}
</style>
